import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {LCol, LRow} from "./Utils";
import UniversalProductDetails from "./FixProductDetails";
import {GraphicCell, NameCell, PriceCell} from "./modalFragments/FixInputFragments";
import {Section, Trigger} from "../../../../../core/components/ui/Expander";
import Outline from "../../../../../core/enum/Outline";

export default class FixProductImmutableMigratedBox extends Component {
    constructor(props) {
        super(props);
    }

    getDetailsSectionHeader() {
        return (
            <Trigger
                className="js-expander__trigger__nested u-inline-block large-offset-by-3 medium-offset-by-3 u-padding-all-l u-no-padding-top">
                <span className="js-expander__trigger--show">{'Zwiń\u00a0szczegóły'}</span>
                <span className="js-expander__trigger--hide">{'Szczegóły'}</span>
            </Trigger>
        );
    }

    getSectionHeader() {
        return (
            <div
                className="opl-box opl-payback-box opl-content-arrow--no-border g-bluef-bg u-padding-m u-padding-top-m">
                <div className="u-no-margin l-row">
                    {
                        this.props.vas.deviceType ?
                            <p className="h6 u-no-margin g-white1-c">{this.props.descriptions.deviceMigratedBoxTitle}</p>
                            :
                            <p className="h6 u-no-margin g-white1-c">{this.props.descriptions.serviceMigratedBoxTitle}</p>
                    }
                </div>
                <div className="u-no-margin-bottom l-row">
                    <div className="l-col l-col-3 large-offset-by-6 medium-offset-by-6">
                        <div className="opl-content-arrow__object opl-content-arrow--no-border g-bluef-b-bg"
                             style={{marginTop: "-10px"}}/>
                    </div>
                </div>
            </div>
        );
    }

    getMigratedProductsQuantity() {
        const groupedProducts = this.props.vas.groupedProducts || [];
        return groupedProducts.filter(p => p.migrated && p.selected).length;
    }

    isWww() {
        return !!this.props.channels && this.props.channels.sales === "WWW";
    }

    render() {
        let outlineStyle = {outline: Outline.INFO};
        const quantity = this.getMigratedProductsQuantity();
        if (quantity > 0) {
            return (
                <div className="u-border-bottom u-position-relative u-margin-top-m"
                     style={{...outlineStyle, marginTop: '2px'}}>
                    {
                        this.getSectionHeader()
                    }
                    <LRow
                        className="u-no-margin u-padding-all-l u-no-padding-bottom u-small-padding-all u-small-padding-top-l"
                        onClick={this.props.onSelectionChanged}>
                        <GraphicCell vas={this.props.vas}/>
                        <NameCell vas={this.props.vas}/>
                        <PriceCell small="12" medium="5" big="5" vas={this.props.vas} net={this.props.net} quantity={quantity}/>
                    </LRow>
                    <Section header={this.getDetailsSectionHeader()}
                             contentClassName="js-expander__container__nested"
                             headerClassName="u-position-relative u-small-text-right"
                             className="l-row u-no-margin opl-section-outer--2"
                             expanded={this.isWww()}
                             styleObject={this.isWww() ? {display: "block"} : null}>
                        <UniversalProductDetails vas={this.props.vas}/>
                    </Section>
                </div>
            );
        }else{
            return null;
        }
    }
}

FixProductImmutableMigratedBox.propTypes = {
    vas: PropTypes.shape({
        title: PropTypes.string,
        id: PropTypes.string,
        paymentDescriptions: PropTypes.shape({
            monthlyPayments: PropTypes.array
        })
    }),
    idx: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    onSelectionChanged: PropTypes.func,
    includedInThePackageTitle: PropTypes.string
}
